export function random(possible, len) {
    let ret = '';

    for (let i = 0; i < len; i += 1) {
        ret += possible[Math.floor(Math.random() * possible.length)];
    }

    return ret;
}

function base64Encode(str) {
    return btoa(unescape(encodeURIComponent(str)));
}

function base64EncodeUrlSafeReplace(c) {
    const base64EncodeUrlSafeChars = {
        '+': '-',
        '/': '_',
    };

    return base64EncodeUrlSafeChars[c];
}

export function base64EncodeUrlSafe(str) {
    return base64Encode(str).replace(/[+/]/g, base64EncodeUrlSafeReplace);
}

export function copyToClipboard(str) {
    const el = document.createElement('textarea');

    el.value = str;
    document.body.appendChild(el);
    el.select();

    document.execCommand('copy');
    document.body.removeChild(el);
}

export function nl2br(str) {
    if (typeof str !== 'string') return str === undefined ? '' : str;

    return str.replace(/\r\n|\n\r|\r|\n/g, '<br>');
}

// 手动复制
// <script src="https://cdnjs.cloudflare.com/ajax/libs/clipboard.js/2.0.4/clipboard.js" integrity="sha256-zcyVODxr75HbGSIdOeK8jLNS3rYq2t5M5qRAO7Kzaec=" crossorigin="anonymous"></script>
export function copy(data) {
    let res = false;

    const input = document.createElement('input');
    input.setAttribute('type', 'text');
    input.setAttribute('readonly', 'readonly');
    input.setAttribute('value', data);
    document.body.appendChild(input);
    input.select();

    // ios需要这函数
    input.setSelectionRange(0, 9999);

    console.log('复制操作开始', data);
    if (document.execCommand('copy')) {
        // document.execCommand('copy');
        res = true;
        console.log('复制成功', data);
    } else {
        console.log('复制失败', data);
    }
    document.body.removeChild(input);
    return res;
}

