import request, { apiResError } from './request';

export function getNewsApk() {
    return request.get('/api/v1/management/ota/pkg/latest/get/version?id=0')
        .then((response) => {
            if (response.data.data.status !== 200 && response.data.data.code !== 0) {
                apiResError(response);
                return Promise.reject();
            }

            return Promise.resolve(response.data.data.packageInfo);
        });
}

export function getWechatConfig() {
    const url = window.location.href.split('#')[0];
    return request.get('/api/v4/user/wechat/signature', {
        url,
    }).then((response) => {
        if (response.data.status !== 200) {
            apiResError(response);
            return Promise.reject();
        }

        return Promise.resolve(response.data.data);
    });
}
