function $uuid() {
    return Math.round(2147483647 * Math.random());
}

export function uuid() {
    try {
        // 优先使用 crypto API 来生成 uuid
        const u32a = new window.Uint32Array(1);
        window.crypto.getRandomValues(u32a);

        return u32a[0] & 2147483647; // eslint-disable-line
    } catch (b) {
        return $uuid();
    }
}

function setContext(key, value) { // eslint-disable-line
    const perfix = '__sentry_context__';
    let originValue;

    try {
        originValue = window.localStorage.getItem(perfix + key);
        if (originValue) {
            return originValue;
        }

        window.localStorage.setItem(perfix + key, value);
        return window.localStorage.getItem(perfix + key);
    } catch (e) { } // eslint-disable-line
}

export function getTagsContext() {
    let deviceInfo = {};

    try {
        deviceInfo = JSON.parse(window.miui.getDeviceInfo()) || {};
    } catch (e) { } // eslint-disable-line

    const tags = {
        device: deviceInfo.device || '',
        device_hash: deviceInfo.device_hash || setContext('device_hash', uuid()),
        model: deviceInfo.model || '',
        nt: deviceInfo.nt || (
            window.navigator.connection && window.navigator.connection.effectiveType
        ),
        package_name: deviceInfo.package_name || window.navigator.appName,
        version_code: deviceInfo.version_code || '',
        version_name: deviceInfo.version_name || window.navigator.appVersion,
        version_release: deviceInfo.version_release || '',
        version_incremental: deviceInfo.version_incremental || '',
        os_version: deviceInfo.os_version || '',
    };

    return tags;
}

export function getUserContext() {
    let userAccountInfo = {};

    try {
        userAccountInfo = JSON.parse(window.miui.getUserAccountInfo()) || {};
    } catch (e) { } // eslint-disable-line

    const user = {
        id: userAccountInfo.user_id || setContext('id', uuid()),
    };

    return user;
}

export function captureException(error, options = {}) {
    try {
        window.Raven.captureException(error, options);
    } catch (e) {
        console.error(e);
    }
}

export function captureMessage(message, options = {}) {
    try {
        window.Raven.captureMessage(message, options);
    } catch (e) {
        console.error(e);
    }
}
