import config from '@/config';
import { getImeiMd5, getUserAccountInfo } from './jsApiV2/client';
import { loadCallback } from './javascript';
import { set, get } from './cache';
import { isMiWebViewSDK } from './ua';

/**
 * 初始化一个 A/B Test；客户端维度划分；默认为 A/B 各为 50%
 *
 * @author 文桥 <wenqiao1@xiaomi.com>
 * @param {*} key
 * @param {number} [abtestRate=0.5]
 * @returns
 */
function initOneABTest(key, abtestRate = 0.5) { // eslint-disable-line
    const nextKey = `__ABTEST__${key}`;

    try {
        const originValue = get(nextKey);
        if (originValue) {
            return Number(originValue);
        }

        const isabtest = Number.EPSILON
            ? abtestRate - Math.random() <= Number.EPSILON
            : Math.random() <= abtestRate;
        set(nextKey, isabtest ? '1' : '0');
        return Number(get(nextKey));
    } catch (e) { // eslint-disable-line
        return false;
    }
}

export default function fmonitor(pid, pname, options = {}) {
    const imeiMd5 = getImeiMd5();
    const deviceInfo = config.deviceInfo;
    const userInfo = getUserAccountInfo();

    window.mfm = window.mfm || {};
    window.mfm.queue = [];
    window.mfm.send = function send(data) {
        const type = (data.type || 'GET').toLowerCase();

        if (window.mfm.logger && window.mfm.logger[type]) {
            return window.mfm.logger[type](data.body, data.url);
        }

        window.mfm.queue.push(data);
        return false;
    };

    const abtestVisibleMonitor = initOneABTest('visibleMonitor');
    const webviewExpid = isMiWebViewSDK() ? 'MiWebViewSDK' : 'WebView';
    window.mfm.config = {
        // disabled: true, // 禁用日志上报功能
        appid: pid,
        appname: pname,
        isVisibleMonitor: abtestVisibleMonitor,
        onajax: false,
        page: false,
        sampleRate: 100, // 统计抽样率
        siteSpeedSampleRate: options.siteSpeedSampleRate || 100, // 网站速度抽样率
        expires: options.expires || 2 * 24 * 60 * 60 * 1000,
        resource: {
            timeout: 0,
            imgTimeout: 0,
        },
        logger: {
            url: 'https://r.browser.miui.com/log/uploadApiLog',
            methods: 'GET',
            body: {
                imei: imeiMd5 || '',
                uid: userInfo.user_id || '',
                did: deviceInfo.device_hash || '',
                appVersion: deviceInfo.version_name || '',
                appName: deviceInfo.package_name || '',
                expid: webviewExpid,
                abtestid: abtestVisibleMonitor ? 'IsVisibleMonitor' : 'AllMonitor',
                chanel: deviceInfo.channel || '',
            },
        },
    };

    loadCallback('https://ssl-cdn.static.browser.mi-img.com/feed/sdk/fmonitor/index.min.js');
}

