// 其他几个文件内部调用
export function openDeeplink(url) {
    location.href = `bikan://${url}`;
}


export function tryCall(funName, ...args) {
    try {
        return window.miui[funName](...args);
    } catch (e) {
        // console.log('调用app方法', funName, ...args, e);
        return false;
    }
}

export function tryCallJSONParse(funName, ...args) {
    try {
        return JSON.parse(window.miui[funName](...args) || '{}');
    } catch (e) {
        // console.log('调用app方法', funName, ...args);
        return false;
    }
}
