import {
    dispatchCustomFocusEvent,
    dispatchCustomBlurEvent,
    dispatchCustomBackKeyEvent,
    dispatchCustomEvent,
} from './event';

function enterTab() {
    console.log('enterTab');
}

function enterWebViewFocused() {
    console.log('enterWebviewFocused');
    dispatchCustomFocusEvent();
}

function leaveWebViewBlured() {
    console.log('leaveWebviewBlured');
    dispatchCustomBlurEvent();
}

function pressBackKey() {
    console.log('pressBackKey');
    dispatchCustomBackKeyEvent();
}
/**
 *
 * fePush 客户端推送信息到前端
 *
 * @param {*} params
 */
function fePush(params) {
    console.log(params);
    dispatchCustomEvent('scanQrmessage', params);
}
window.JSRegister = {
    enterTab,
    enterWebViewFocused,
    leaveWebViewBlured,
    pressBackKey,
    fePush,
};


window.enterTab = enterTab;
window.taskSignIn = enterWebViewFocused;
