export function isXiangkan() {
    const UA = navigator.userAgent;

    return UA.indexOf('xiangkan') > -1 || UA.indexOf('bikan') > -1;
}

export function isWechat() {
    const UA = navigator.userAgent;

    return /MicroMessenger/i.test(UA);
}

export function isQQ() {
    const UA = navigator.userAgent;

    return /QQ/i.test(UA);
}

export function isMiuiBrowser() {
    const UA = navigator.userAgent;

    return /XiaoMi\/MiuiBrowser\/([\d\.]+)(-Beta)?$/.test(UA); // eslint-disable-line
}

export function isIOS() {
    const u = navigator.userAgent;

    return /(iPhone|iPad|iPod|iOS)/i.test(u);
}

export function isAndroid() {
    const u = navigator.userAgent;

    return /(Android)/i.test(u);
}

export function isMiWebViewSDK() {
    const u = navigator.userAgent;
    const regexp = /Chrome\/([\d.]+)/i;
    const matchs = u.match(regexp) || [];
    const chromeVersion = matchs[1];
    const isMB = u.indexOf('XiaoMi/MiuiBrowser') > -1;

    if (isMB && chromeVersion && parseFloat(chromeVersion) >= 71) {
        return true;
    }

    return false;
}
