import { getStatusBarHeight, changeStatusBarDarkMode } from '../utils/jsApiV2/client';
import { addCustomEventListener } from '../utils/event';


export default {
    inserted: ($el, binding, vNode) => {
        const query = vNode.context.$route.query || {};
        const bool = binding.value;

        if (query.full_screen) {
            $el.style.paddingTop = `${getStatusBarHeight() / window.devicePixelRatio}px`; // eslint-disable-line
            changeStatusBarDarkMode(bool);

            addCustomEventListener('focus', () => {
                changeStatusBarDarkMode(bool);
            });
        }
    },
};
