import o2o, { expose } from '@/utils/tracker';
import { apiCaptureException } from '@/api/monitor';

export default {
    methods: {
        $error() {
            this.$router.push({
                name: 'error',
            });
        },
        $o2o(args, success, error) {
            let ext = null;
            try {
                // 如果有url中有来源信息xkref,在ext中拓展reftype字段
                ext = typeof args.e_x === 'string' ? JSON.parse(args.e_x) : (args.e_x || {});
                if (this.$route.query.xkref) {
                    ext.reftype = this.$route.query.xkref;
                }
            } catch (e) {
                console.error(e);
            }
            return o2o({ ...args, e_x: JSON.stringify(ext || args.e_x) }, success, error);
        },
        $expose(app, tracker) {
            return expose(app, tracker);
        },
        $apiError(data) {
            apiCaptureException(data);
        },
    },
};
