import { tryCall, tryCallJSONParse } from './index';

// 短信邀请
/**
 * 获取联系人的权限
 * @export
 * @param {*} callback
 */
export function checkContactsPermission(callback) {
    return tryCall('checkContactsPermission', callback);
}
/**
 * 首次进入时请求联系人列表，调用的方法
 * @export
 * @param {*} callback
 * @returns
 */
export function checkPermissionAndGetContactList(callback) {
    return tryCall('checkPermissionAndGetContactList', callback);
}

/**
 * 根据phone md5 list，回调cb返回联系人姓名手机号等信息
 * @export
 * @param {*} phoneMd5Str 加密之后的手机号码
 * @param {*} callback 回调函数
 * @returns
 */
export function getContactsInfo(phoneMd5Str, callback) {
    return tryCall('getContactsInfo', phoneMd5Str, callback);
}

/**
 * 群发短信
 * @export
 * @param {*} phoneMd5Str
 * @param {*} callback
 * @returns
 */
export function sendMessage(phoneMd5Str, callback) {
    return tryCall('sendMessage', phoneMd5Str, callback);
}

// 走路赚钱
/**
 * wiki: https://wiki.n.miui.com/pages/viewpage.action?pageId=242620928
 * @param {*} callback
 */
export function isSupportSteps(callback) {
    return tryCall('isSupportSteps', callback);
}

/**
 * @param {*} callback
 */
export function getTodaySteps(callback) {
    return tryCall('getTodaySteps', callback);
}

/**
 * 调起分享弹框
 * @param {String} title
 * @param {String} url
 * @param {String} text
 * @param {String} thumbUrl
 */
export function showShareDialog({ title, url, text, thumbUrl }) {
    return tryCall('share', title, url, text, thumbUrl);
}

export function shareGroundPromotionPoster(...ret) {
    console.log(...ret);
    return tryCall('shareGroundPromotionPoster', ...ret);
}

/**
 * 邀请码分享，4.4.0 及以后版本支持
 * @param title
 * @param url
 * @param text
 * @param thumbUrl
 * @param socialType QQ 1, 微信 2, 朋友圈 3
 */
export function shareInvitationCode(title, shareUrl, summary, imageUrl, channel) {
    return tryCall('shareInvitationCode', title, shareUrl, summary, imageUrl, channel);
}

/**
 * 文字分享
 * @param text
 * @param socialType QQ 1, 微信 2, 朋友圈 3
 */
export function shareText(text, channel) {
    return tryCall('shareText', text, channel);
}

/**
 * 指定渠道分享接口 3.2.0及以后版本支持
 * 分享至社交app
 * @param {String} title
 * @param {String} url
 * @param {String} text
 * @param {String} thumbUrl
 * @param {String} socialType 1(QQ)/2(wechat)/3(moment)
 */
export function shareWithSocialType({ title, url, text, thumbUrl, socialType }) {
    return tryCall('share', title, url, text, thumbUrl, socialType);
}

/**
 * 任务页签到
 * @param {String} signedDays 连续签到天数
 */
export function signIn(signedDays) {
    return tryCall('signIn', signedDays);
}


/**
 * 红包雨（4.3.3）
 */
export function openTimeLimitPacket() {
    return tryCall('openTimeLimitPacket');
}

/**
 * 红包雨（4.3.3）
 */
export function getTimeLimitPacketConfig() {
    return tryCallJSONParse('getTimeLimitPacketConfig') || {};
}

// 打开指定包名
export function openApp({ packageName }) {
    return tryCall('openApp', packageName);
}

