export default {
    $loading: null,
    inserted: ($el) => {
        this.$loading = document.createElement('div');
        this.$loading.classList.add('page-loading');

        $el.insertAdjacentElement('afterbegin', this.$loading);
    },
    update: (_, binding) => {
        if (binding.value) {
            this.$loading.classList.add('page-loading');
            this.$loading.classList.remove('hide');
        } else {
            this.$loading.classList.remove('page-loading');
            this.$loading.classList.add('hide');
        }
    },
};
