import axios from 'axios';

export const statsUrl = 'https://o2o.api.xiaomi.com/tracker';
export const statsAppId = '2882303761517406076';
export const statsToken = '5471740665076';
export const h5statsAppId = '2882303761517406118';
export const h5statsToken = '5531740676118';

export function stats(data, success, error) {
    const instance = axios.create();
    instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

    return instance.post(statsUrl, data)
        .then(success)
        .catch(error);
}
