function isHidden($elem) {
    let style;

    // eslint-disable-next-line no-param-reassign
    for (; $elem; $elem = $elem.parentElement) {
        // eslint-disable-line
        style = getComputedStyle($elem);

        if (
            style.display === 'none' ||
            style.visibility === 'hidden' ||
            style.opacity === 0
        ) {
            return true;
        }
    }

    return false;
}

export function getElementById(id) {
    return window.document.getElementById(id);
}

export function getElementsByClassName(classname) {
    return window.document.getElementsByClassName(classname);
}

export function show(selector) {
    const ele = window.document.querySelector(selector);

    if (ele && ele.style && ele.style.display === 'none') {
        ele.style.display = '';
    }
}

export function css(selector, style) {
    const ele = window.document.querySelector(selector);
    const keys = Object.keys(style);
    let styleStr = '';

    keys.forEach((key) => {
        const item = `${key.replace(/([A-Z])/g, '-$1')}: ${style[key]}; `;
        styleStr += item;
    });

    if (ele) {
        const cssText = ele.style.cssText;
        ele.style = cssText + styleStr;
    }
}

export function inViewport($elem, threshold = {}, viewportRect = {}) {
    // eslint-disable-line
    const rect = $elem.getBoundingClientRect();
    const $html = document.documentElement;

    // eslint-disable-next-line no-param-reassign
    threshold = Object.assign(
        {
            // eslint-disable-line
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
        },
        typeof threshold === 'number'
            ? {
                top: threshold,
                bottom: threshold,
                left: threshold,
                right: threshold,
            }
            : threshold,
    );

    // eslint-disable-next-line no-param-reassign
    viewportRect = Object.assign(
        {
            // eslint-disable-line
            top: 0,
            bottom: $html.clientHeight,
            left: 0,
            right: $html.clientWidth,
        },
        viewportRect,
    );

    return (
        !isHidden($elem) &&
        rect.width &&
        rect.height &&
        rect.bottom > viewportRect.top - threshold.top &&
        rect.right > viewportRect.left - threshold.left &&
        rect.top < viewportRect.bottom + threshold.bottom &&
        rect.left < viewportRect.right + threshold.right
    );
}
