import { stats, statsAppId, statsToken, h5statsAppId, h5statsToken } from '../api/o2o';
import * as string from './string';
import * as jsApi from './jsApiV2/client';
import * as cache from './cache';
import { inViewport } from './dom';
import { isXiangkan } from './ua';

const queryString = require('qs');
const debounce = require('lodash.debounce');

const sessionTimeout = 30 * 60 * 1000;
const sessionIdKey = 'SessionId';
const sessionTimeKey = 'SessionTime';

function getO2oConfig() {
    if (isXiangkan()) {
        return {
            app_id: statsAppId,
            token_auth: statsToken,
        };
    }

    return {
        app_id: h5statsAppId,
        token_auth: h5statsToken,
    };
}

function getSessionId() {
    const now = Date.now();
    const sessionId = cache.sget(sessionIdKey);
    const lastTime = parseInt(cache.sget(sessionTimeKey), 10);

    if (sessionId && lastTime && now >= lastTime && now <= lastTime + sessionTimeout) {
        return sessionId;
    }

    cache.sset(sessionTimeKey, now);
    cache.sset(sessionIdKey, string.random('0123456789abcdef', 32));

    return cache.sget(sessionIdKey);
}

export default function o2o(args, success = () => { }, error = () => { }) {
    const userAccountInfo = jsApi.getUserAccountInfo();
    const o2oConfig = getO2oConfig();

    let baseArgs = {
        app_id: o2oConfig.app_id,
        token_auth: o2oConfig.token_auth,
        uid: userAccountInfo.user_id || '',
        did: jsApi.getImeiMd5(),
        _id: getSessionId(),
        cdt: Date.now(),
        e_c: args.e_c,
        e_a: args.e_a,
        e_n: args.e_n,
        e_x: args.e_x || '',
    };

    const deviceInfo = jsApi.getDeviceInfo();

    if (deviceInfo.device_hash) {
        baseArgs = {
            ...baseArgs,
            oaid: deviceInfo.oaid,
            cv: deviceInfo.version_name,
            network: deviceInfo.nt,
            channel: deviceInfo.channel,
            res: `${deviceInfo.screen_width}x${deviceInfo.screen_height}`,
            model: deviceInfo.model,
            os_version: deviceInfo.os_version,
            ua: `MiuiBrowser (Linux; U; Android ${deviceInfo.version_release} ; ${deviceInfo.model}MIUI/${deviceInfo.version_incremental})`,
            lang: deviceInfo.language && deviceInfo.language.split('-')[0],
            country: deviceInfo.region,
            eid: deviceInfo.eid, // 实验id
        };
    }
    console.log(baseArgs);

    const data = queryString.stringify({
        requests: JSON.stringify({
            requests: [`?${queryString.stringify(baseArgs)}`],
            token_auth: statsToken,
        }),
    });

    return stats(data, success, error);
}

export function exposeElement(element, tracker) {
    const rect = element.getBoundingClientRect();
    const distance = 0 - ((rect.height * 3) / 10);
    const noNeedExpose = element.dataset.isexpose === '1';

    if (inViewport(element, { top: distance, bottom: distance }, {}) && !noNeedExpose) {
        element.dataset.isexpose = '1'; // eslint-disable-line

        tracker(JSON.parse(element.dataset.expose));
    }
}

function sendExpose(tracker = o2o) {
    const customize = document.querySelectorAll('.customize-expose') || [];

    function send(target) {
        if (!target) {
            return false;
        }

        exposeElement(target, tracker);

        return true;
    }

    for (let i = 0, len = customize.length; i < len; i += 1) {
        send(customize[i]);
    }
}

export function expose(app = window, tracker) {
    const delay = 50;
    let timer = setTimeout(() => {
        sendExpose(tracker);
    }, delay);

    app.addEventListener('scroll', debounce(() => {
        timer = setTimeout(() => {
            sendExpose(tracker);
        }, delay);
    }, 50), { capture: true, passive: true });

    app.addEventListener('scroll', () => {
        clearTimeout(timer);
    }, { capture: true, passive: true });
}
