import conf from '@/config';
import request, { apiResError } from './request';
import { set, get, remove } from '../utils/cache';

export function test() {}

// https://wiki.n.miui.com/pages/viewpage.action?pageId=141230752
export function getConfigByKey(data = {}) {
    if (!data.key) {
        return Promise.reject('缺少必要参数key');
    }

    let config;

    try {
        config = JSON.parse(get(`config-${data.key}`));
    } catch (e) {
        remove(`config-${data.key}`);
    }

    const setConfig = options => request.get('/api/v4/config/web-client', options)
        .then((response) => {
            if (response.data.status !== 200) {
                return Promise.reject(response.data.data);
            }

            if (response.data.data === '') {
                return Promise.reject(response.data.msg);
            }

            try {
                config = {
                    status: 200,
                    data: JSON.parse(response.data.data),
                };
            } catch (e) {
                console.log(e, 'config');
                return Promise.reject(response.data);
            }

            if (config.data.maxAge) {
                config.expires = config.data.maxAge + Date.now();
                delete config.data.maxAge;
                set(`config-${options.key}`, JSON.stringify(config));
                delete config.expires;
                return Promise.resolve(config);
            }

            if (config.data.reloadRefresh === true) {
                config.reloadRefresh = true;
                delete config.data.reloadRefresh;
                set(`config-${options.key}`, JSON.stringify(config));
                delete config.reloadRefresh;
                return Promise.resolve(config);
            }
            return Promise.resolve(config);
        });

    if (config && config.reloadRefresh === true) {
        setConfig(data);
        delete config.reloadRefresh;

        return Promise.resolve(config);
    } else if (config && Number(config.expires) > Date.now()) {
        delete config.expires;

        return Promise.resolve(config);
    }

    return setConfig(data);
}

export function getClientConfig() {
    return request.get('/api/v4/config')
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);

                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}

export function getServerConfigByKey(key = '') {
    return request.get(`/api/v1/config/${key}`)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);

                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}

export function getShortUrl(data = {}) {
    return request.post(`${conf.origin.hd}/api/shorturl/create`, data)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);

                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}
