/* global wx  */

import { getNewsApk, getWechatConfig } from '@/api/share';
import { getShortUrl } from '@/api/common';
import config from '@/config';
import { isWechat, isQQ, isMiuiBrowser } from '@/utils/ua';
import { shareText } from '@/utils/jsApiV2/act';
import { toast } from '@/utils/jsApiV2/client';

// import { copy } from '@/utils/string';
// import { getApkVersion, setCallbackForNotifyAppLaunch } from '@/utils/jsApi';

let wechatConfig = null;
// 视频详情页和社区分享页  保持和主端一致 增加参数
export const cKey = 'CK1430771235862';

export const shareChannelMap = {
    wechat: '微信',
    qq: 'QQ',
    moment: '朋友圈',
    social: '二次分享',
};

export const shareSourceNameMap = {
    xiangkan: '想看',
    miuibrowser: '浏览器',
};

export function getShareChannel(v) {
    return shareChannelMap[v];
}

export function getShareSourceName(source = 'xiangkan') {
    return shareSourceNameMap[source];
}

export function goAppStore(schema, ckey) {
    let uri;

    if (isMiuiBrowser()) {
        uri = config.miAppStoreDeeplink;
    } else if (ckey) {
        uri = `${config.appStore}&ckey=${ckey}&android_schema=${encodeURIComponent(schema)}`;
    } else {
        uri = `${config.appStore}&android_schema=${encodeURIComponent(schema)}`;
    }

    location.href = uri;
}

export function goApp(schema, downloadAppUrl, options = {}) {
    let uri = `${config.appStore}&android_schema=${encodeURIComponent(schema)}`;
    if (options.ckey) {
        uri = `${uri}&ckey=${options.ckey}`;
    }

    if (isWechat()) {
        location.href = uri;
    } else {
        if (downloadAppUrl) {
            const download = document.createElement('a');

            download.href = downloadAppUrl;
            download.click();

            return;
        }

        getNewsApk().then((res) => {
            const download = document.createElement('a');

            download.href = res.pkgUrl;
            download.click();
        }).catch(() => {
            // 获取apk地址失败，跳应用商店
            goAppStore(schema || 'bikan://web.tab/newsTab');
        });
    }
}

export function downloadChannelApk(channel) {
    const download = document.createElement('a');
    download.href = `https://feed.browser.miui.com/api/v1/package/${channel}`;
    download.click();
}

export function deeplinkApp(schema, callback = () => { }, ckey) {
    if (isWechat()) {
        goAppStore(schema, ckey);
    }

    const ifr = document.createElement('iframe');

    ifr.src = schema;
    ifr.style.display = 'none';
    document.body.appendChild(ifr);

    // 记录唤醒时间
    const openTime = +new Date();
    callback({ e_n: '跳转APP弹窗' });

    setTimeout(() => {
        document.body.removeChild(ifr);
        // 如果setTimeout 回调超过2500ms，代表成功调起
        if ((+new Date()) - openTime < 2500) {
            goAppStore(schema, ckey);
            callback({ e_n: '调起下载弹窗' });
        }
    }, 2000);
}

export function tryDeeplink(schema, opt = {}) {
    if (isWechat()) {
        if (opt.fail) {
            opt.fail();
        }
        return;
    }

    const ifr = document.createElement('iframe');

    ifr.src = schema;
    ifr.style.display = 'none';
    document.body.appendChild(ifr);

    const openTime = +new Date();

    setTimeout(() => {
        document.body.removeChild(ifr);
        // 如果setTimeout 回调超过2500ms，代表成功调起
        if ((+new Date()) - openTime < 2500) {
            if (opt.fail) {
                opt.fail();
            }
        } else if (opt.success) {
            opt.success();
        }
    }, 2000);
}

export function wechatAuth(url) {
    location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx64f017709ff9fc6e&redirect_uri=${encodeURIComponent(url)}&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect`;
}

export function getShareUrl(long) {
    return getShortUrl({
        type: '1',
        url: long,
    });
}

export function goBrowerDownload(opt = {}) {
    const isBrowser = location.search.includes('mi_source=miuibrowser');
    const ckey = isBrowser ? 'CK1433782967648' : 'CK1433533136477';

    // 统一到应用宝
    window.location.href = `http://a.app.qq.com/o/simple.jsp?pkgname=com.xiangkan.android&ckey=${ckey}&android_schema=${encodeURIComponent(
        opt.schema,
    )}`;
}

export function initShareConfig() {
    return new Promise((resolve) => {
        const $script = document.createElement('script');

        $script.addEventListener('load', () => {
            getWechatConfig()
                .then((res) => {
                    resolve(res);
                });
        });

        $script.src = config.wechatSdk;
        document.body.appendChild($script);
    });
}

// 微信端外 自定义分享内容到朋友，朋友圈，QQ，QQ空间
// https://wiki.n.miui.com/pages/viewpage.action?pageId=293609887
export async function setWechatShareInfo({ title, summary, pic, url }) {
    if (!wechatConfig) {
        wechatConfig = await initShareConfig();
    } else {
        wechatConfig = await new Promise((resolve) => {
            getWechatConfig().then((res) => {
                resolve(res);
            });
        });
    }

    wx.config({
        debug: false,
        appId: wechatConfig.appId,
        timestamp: wechatConfig.timestamp,
        nonceStr: wechatConfig.nonceStr,
        signature: wechatConfig.signature,
        jsApiList: wechatConfig.jsApiList,
    });

    wx.ready(() => {
        const shareData = {
            title,
            desc: summary,
            link: url,
            imgUrl: pic,
            success() {
                console.log('微信分享配置成功');
            },
        };
        wx.updateAppMessageShareData(shareData);
        wx.updateTimelineShareData(shareData);
    });
}

// QQ端外 自定义分享内容  无需考虑重复引入script问题
// https://wiki.n.miui.com/pages/viewpage.action?pageId=271455094
export function setQQShareInfo({ title, summary, pic, url }) {
    const $script = document.createElement('script');
    $script.src = config.qqShareSdk;
    document.body.appendChild($script);
    $script.addEventListener('load', () => {
        window.setShareInfo({
            title,
            summary,
            pic,
            url,
        });
    });
}

// eslint-disable-next-line consistent-return
export function setShareInfo({ title, summary, pic, url }) {
    if (isWechat()) {
        setWechatShareInfo({ title, summary, pic, url });
    } else if (isQQ()) {
        setQQShareInfo({ title, summary, pic, url });
    } else {
        return false;
    }
}

function shareCodeShortUrl(url, type = 'sharePage') {
    getShareUrl(url).then((shortUrl) => {
        let content = `帮我点一下，咱俩都有钱拿\n${shortUrl}`;
        if (type === 'store') {
            content = `邀你一起来赚钱[爱心]秒提现\n${shortUrl}点击下载[想看]，登录立领2元，下载后再次点击这里立领。`;
        }
        shareText(content, 2);
    }).catch(() => {
        toast('获取分享地址失败,再试一次吧');
    });
}

export function shareCodeTextToWechat(url, code) {
    if (Math.random() > 0.5) {
        shareCodeShortUrl(url, 'sharePage');
    } else {
        shareCodeShortUrl(`http://a.app.qq.com/o/simple.jsp?pkgname=com.xiangkan.android&ckey=CK1441826083026&android_schema=${
            encodeURIComponent(`bikan://goto/inviteCode?content=${code}`)
        }`, 'store');
    }
}
