const perfix = 'MIFEEDS_MOBILE_V2';

export function set(key, value) {
    return localStorage.setItem(`${perfix}_${key}`, value);
}

export function get(key) {
    return localStorage.getItem(`${perfix}_${key}`);
}

export function remove(key) {
    return localStorage.removeItem(`${perfix}_${key}`);
}

// export function clear() {
//     return localStorage.clear();
// }

export function sset(key, value) {
    return sessionStorage.setItem(`${perfix}_${key}`, value);
}

export function sget(key, value) {
    return sessionStorage.getItem(`${perfix}_${key}`, value);
}

export function sremove(key) {
    return sessionStorage.removeItem(`${perfix}_${key}`);
}

