function isWebStorageSupported(type) {
    const testKey = 'test';
    const storage = window[type];

    try {
        storage.setItem(testKey, 'testValue');
        storage.removeItem(testKey);

        return true;
    } catch (error) {
        return false;
    }
}

// https://developer.mozilla.org/zh-CN/docs/Web/API/Storage/LocalStorage
function createStorage(storage = 'localStorage') {
    if (!isWebStorageSupported(storage)) {
        Object.defineProperty(window, storage, new (function handler() {
            const aKeys = [];
            const oStorage = {};
            const expiresStringMap = {
                localStorage: '; expires=Tue, 19 Jan 2038 03:14:07 GMT; path=/',
                sessionStorage: '; path=/',
            };

            Object.defineProperty(oStorage, 'getItem', {
                value(sKey) { return sKey ? this[sKey] : null; },
                writable: false,
                configurable: false,
                enumerable: false,
            });
            Object.defineProperty(oStorage, 'key', {
                value(nKeyId) { return aKeys[nKeyId]; },
                writable: false,
                configurable: false,
                enumerable: false,
            });
            Object.defineProperty(oStorage, 'setItem', {
                value(sKey, sValue) {
                    if (!sKey) { return; }
                    document.cookie = `${escape(sKey)}=${escape(sValue)}${expiresStringMap[storage]}`;
                },
                writable: false,
                configurable: false,
                enumerable: false,
            });
            Object.defineProperty(oStorage, 'length', {
                get() { return aKeys.length; },
                configurable: false,
                enumerable: false,
            });
            Object.defineProperty(oStorage, 'removeItem', {
                value(sKey) {
                    if (!sKey) { return; }
                    document.cookie = `${escape(sKey)}=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/`;
                },
                writable: false,
                configurable: false,
                enumerable: false,
            });
            this.get = () => {
                let iThisIndx;
                const oStorageKeys = Object.keys(oStorage);

                /*
                 * 将oStorage中不包含在aKeys中的键，写入cookie
                 * 并清空oStorage
                 */
                oStorageKeys.forEach((sKey) => {
                    iThisIndx = aKeys.indexOf(sKey);

                    if (iThisIndx === -1) {
                        oStorage.setItem(sKey, oStorage[sKey]);
                    } else {
                        aKeys.splice(iThisIndx, 1);
                    }

                    delete oStorage[sKey];
                });

                // 清空aKeys
                for (aKeys; aKeys.length > 0; aKeys.splice(0, 1)) { oStorage.removeItem(aKeys[0]); }

                // 重新根据cookie写oStorage和aKeys
                for (let aCouple, iKey, nIdx = 0, aCouples = document.cookie.split(/\s*;\s*/); nIdx < aCouples.length; nIdx += 1) {
                    aCouple = aCouples[nIdx].split(/\s*=\s*/);
                    if (aCouple.length > 1) {
                        oStorage[iKey = unescape(aCouple[0])] = unescape(aCouple[1]);
                        aKeys.push(iKey);
                    }
                }

                return oStorage;
            };
            this.configurable = false;
            this.enumerable = true;
        })());
    }
}

export function storageFallback() {
    createStorage('localStorage');
    createStorage('sessionStorage');
}
