function getEventName() {
    if (/bikan/i.test(navigator.userAgent)) {
        return {
            focus: 'CFocus',
            blur: 'CBlur',
        };
    }

    return {
        focus: 'focus',
        blur: 'blur',
    };
}

export function dispatchCustomFocusEvent() {
    const eventName = getEventName();
    const event = new Event(eventName.focus);

    window.dispatchEvent(event);
}

export function dispatchCustomBlurEvent() {
    const eventName = getEventName();
    const event = new Event(eventName.blur);

    window.dispatchEvent(event);
}

export function dispatchCustomBackKeyEvent() {
    const event = new Event('backKey');
    window.dispatchEvent(event);
}

/**
 *
 * @param {*} key  代表需要dispatch的值
 * for example:
 * message: 客户端向前端推送消息  from: jsRegister.js->fePush
 */
export function dispatchCustomEvent(key, data) {
    window.dispatchEvent(new CustomEvent(key, { detail: data }));
}

export function addCustomEventListener(type, listener) {
    const eventName = getEventName();
    window.addEventListener(eventName[type] || type, listener);
}

export function removeCustomEventListener(type, listener) {
    const eventName = getEventName();
    window.removeEventListener(eventName[type] || type, listener);
}
