import conf from '@/config';
import * as jsApi from '../utils/jsApiV2/client';
import { captureException } from '../utils/sentry';
import request from './request';

const queryString = require('qs');

export function injectRequest(config) {
    let nextConfig;

    // FIXME: catch window.performance.now not a function
    try {
        nextConfig = Object.assign({}, config, { requestStartTime: window.performance.now() });
    } catch (e) {
        console.error(1, e);
    }

    return nextConfig || config;
}

function beaconPing(data, url = 'https://r.browser.miui.com/log/uploadApiLog', callback = () => {}) {
    const headers = { type: 'application/x-www-form-urlencoded;charset=UTF-8' };
    const blob = new window.Blob([queryString.stringify(data)], headers);

    // 不求百分百上报，达到统计目的就好；Andorid 5 以上支持
    if (window.navigator.sendBeacon) {
        const isSend = window.navigator.sendBeacon(
            url,
            blob,
        );

        callback(isSend, data, url);
        return isSend;
    }

    callback(false, data, url);
    return false;
}

export function sendResquestConsumingTime(response, pid, pname) {
    // 日志上报接口不统计
    if (response.config.url.indexOf('/log/uploadApiLog') > -1) {
        return;
    }

    const imei = jsApi.getImeiMd5();
    const userAccountInfo = jsApi.getUserAccountInfo();
    const query = queryString.parse(response.config.url.replace(/^.*\?/, ''));
    const url = response.config.url.replace(/\?.*$/, '');

    // //www.miui.com/bbshd/share/weixin
    // https://www.miui.com/bbshd/share/weixin
    const matched = url.match(/^((https?:)?\/\/)?([^\/]*)?(\/.*)$/) || []; // eslint-disable-line
    const hostname = matched[3] || location.hostname;
    const pathname = matched[4] || location.pathname;

    const responseTime = window.performance.now() - response.config.requestStartTime;
    const argus = Object.assign({}, query, response.config.data
        || {}, response.config.params || {});
    const apiCode = response.data.status
        || (response.data.data && response.data.data.code) || '0000';
    const network = jsApi.getActiveNetworkTypeName()
        || (window.NetworkInformation
                && window.NetworkInformation.effectiveType
                && window.NetworkInformation.effectiveType.toUpperCase()) || 'UNKNOWN';
    const body = {
        pid,
        pname,
        imei,
        uid: userAccountInfo.user_id || '-1',
        type: 'e2e',
        hostname,
        pathname,
        ext: JSON.stringify(argus),
        api_code: apiCode,
        api_status: response.status,
        api_time: Math.round(responseTime),
        app_name: network,
        app_version: window.location.href,
        ua: window.navigator.userAgent,
        timestamp: Date.now(),
    };

    beaconPing(body);
}

export function apiCaptureException(data) {
    // 日志上报接口不统计
    if (data.request.url.indexOf('/log/uploadApiLog') > -1) {
        return;
    }

    let pid;
    let pname;
    if (process.env.npm_config_online === 'true') {
        pid = 'e0c16146fb0142eda73eee618f70c244';
        pname = 'mifeeds-mobile-v2';
    } else {
        pid = 'b5a069a78c2748a1a446c77ceeef2800';
        pname = 'mifeeds-mobile-v2-staging';
    }

    const imei = jsApi.getImeiMd5();
    const userAccountInfo = jsApi.getUserAccountInfo();
    const hostname = window.location.hostname;
    const pathname = data.request.url;
    const ext = data;
    const apiCode = (data.response.data
        && (data.response.data.status || data.response.data.returnCode || data.response.data.code))
        || '0000';

    const body = {
        pid,
        pname,
        imei,
        uid: userAccountInfo.user_id || '-1',
        type: 'API_ERROR',
        hostname,
        pathname,
        ext: JSON.stringify(ext),
        api_code: apiCode,
        api_status: data.response.status || '000',
        app_name: conf.deviceInfo.package_name || window.navigator.appName,
        app_version: window.location.href,
        ua: window.navigator.userAgent,
        timestamp: Date.now(),
    };

    captureException(`Error in Request ${data.request.url}`, {
        level: 'error',
        logger: 'API_ERROR',
        extra: data,
    });
    request.spost('https://r.browser.miui.com/log/uploadApiLog', body);
}
