import Vue from 'vue';
import Swipe from 'vant/lib/swipe';
import SwipeItem from 'vant/lib/swipe-item';
import Toast from 'vant/lib/toast';
import List from 'vant/lib/list';
import ImagePreview from 'vant/lib/image-preview';
import Actionsheet from 'vant/lib/actionsheet';
import VueLazyload from 'vue-lazyload';
import 'vant/lib/list/style';
import 'vant/lib/image-preview/style';
import 'vant/lib/actionsheet/style';

import './registerServiceWorker';
// import './unRegisterServiceWorker';
import App from './App';
import router from './router';
import { loadCallback } from './utils/javascript';
import { getTagsContext, getUserContext } from './utils/sentry';
import { sget, sset, storageFallback } from './utils/cache';
import fmonitor from './utils/fmonitor';
import './utils/jsRegister';
import './assets/css/reset.css';
import './assets/css/clearfix.css';
import './assets/css/hairline.css';
import './assets/css/vant.css';
import './assets/css/common.css';
import './assets/css/video-js.css';

import mfPlugin from './plugins';

Vue.config.productionTip = false;

if (process.env.NODE_ENV === 'development') {
    Vue.config.devtools = true;
}

Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Toast);
Vue.use(List);
Vue.use(ImagePreview);
Vue.use(mfPlugin);
Vue.use(Actionsheet);
Vue.use(VueLazyload);

const placeholder = window.document.querySelector('#placeholder');

storageFallback();
try {
    window.PerformanceTiming.firstMeaningfulPaintTime = NaN;
} catch (e) {
    console.error(e);
}

/* eslint-disable no-new */
window.app = new Vue({
    el: '#app',
    methods: {
        setFirstMeaningfulPaintTime() {
            try {
                window.PerformanceTiming.firstMeaningfulPaintTime = Date.now() - window.performance.timing.fetchStart;
            } catch (e) {
                console.error(e);
            }
        },
        // TODO: 所有路由下都要做处理
        hidePlaceholder() {
            placeholder.style.display = 'none';
            this.setFirstMeaningfulPaintTime();
        },
        showPlaceholder() {
            placeholder.style.display = 'block';
        },
        // FIXME: 也可以考虑全局统一处理
        setBackflow(customUrl) {
            const url = location.href;

            const backPage = customUrl ? `${location.origin}${customUrl}` : `${location.origin}/mobile-v2/`;
            const backflow = this.$router.currentRoute.query.backflow || '1';

            // 禁止展示回流页面
            if (backflow === '0') {
                return;
            }

            // 二次刷新页面
            if (sget('backflow') === '1') {
                return;
            }

            const state = { page: 'backflow', url };

            // 首次打开页面 backflow 标识
            sset('backflow', '1');
            window.history.replaceState(state, null, backPage);
            window.history.pushState(state, null, url);
        },
    },
    router,
    render: h => h(App),
});

