import axios from 'axios';
import md5 from 'md5';
import config from '@/config';
import * as jsApi from '../utils/jsApiV2/client';
import { injectRequest, sendResquestConsumingTime, apiCaptureException } from './monitor';
import { uuid } from '../utils/sentry';

const queryString = require('qs');

axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.put['Content-Type'] = 'application/json';
axios.defaults.timeout = 10000;

export function errorFormatter(error) {
    try {
        // https://github.com/axios/axios/blob/b681e919c40fbf7abd109d0cb4f0caf2fd5553e5/lib/core/enhanceError.js
        const jsonString = JSON.stringify(error);
        const errorObject = JSON.parse(jsonString);
        // https://github.com/axios/axios/tree/b681e919c40fbf7abd109d0cb4f0caf2fd5553e5#axios-api
        const data = {
            request: {
                method: errorObject.config.method,
                url: errorObject.config.url.replace(/\?.*$/, ''),
                timeout: errorObject.config.timeout,
                data: Object.assign(
                    {},
                    errorObject.config.params || {},
                    errorObject.config.data || {},
                ),
            },
            response: {
                status: errorObject.response && errorObject.response.status,
                statusText: errorObject.response && errorObject.response.statusText,
                data: errorObject.response && errorObject.response.data,
            },
        };

        return data;
    } catch (e) {
        console.error(e);
    }

    return null;
}

export function responseFormatter(response) {
    const data = {
        request: {
            method: response.config.method,
            url: response.config.url.replace(/\?.*$/, ''),
            timeout: response.config.timeout,
            data: Object.assign({}, response.config.params || {}, response.config.data || {}),
        },
        response: {
            status: response.status,
            statusText: response.statusText,
            data: response.data,
        },
    };

    return data;
}

export function apiResError(response) {
    try {
        const formatedRes = responseFormatter(response);

        apiCaptureException(formatedRes);
    } catch (e) {
        console.error(e);
    }
}

export function requestError(error) {
    try {
        const formatedError = errorFormatter(error) || {};

        apiCaptureException(formatedError);
    } catch (e) {
        console.error(e);
    }
}

// Add a request interceptor
axios.interceptors.request.use((resConfig) => {
    // Do something before request is sent
    const nextResConfig = injectRequest(resConfig);
    return nextResConfig;
}, (error) => {
    // Do something with request error
    // TODO: 400 等
    requestError(error);

    return Promise.reject(error);
});

// Add a response interceptor
axios.interceptors.response.use((response) => {
    // Do something with response data
    try {
        if (process.env.npm_config_online === 'true') {
            sendResquestConsumingTime(response, 'e0c16146fb0142eda73eee618f70c244', 'mifeeds-mobile-v2');
        } else {
            sendResquestConsumingTime(response, 'b5a069a78c2748a1a446c77ceeef2800', 'mifeeds-mobile-v2-staging');
        }
    } catch (e) {
        console.error('API Monitor Throw an Error.', e);
    }
    return response;
}, (error) => {
    // Do something with response error
    // TODO: 502/404 等
    requestError(error);

    return Promise.reject(error);
});

// https://wiki.n.miui.com/pages/viewpage.action?pageId=102411966
export function getCommonData() {
    const devinfo = config.deviceInfo;
    const userInfo = jsApi.getUserAccountInfo();
    const imei = jsApi.getImeiMd5();

    const queryObj = {
        version_name: devinfo.version_name || '',
        version_code: devinfo.version_code || 100000, // 签名必须要version_code大于0
        net: devinfo.nt || '',
        oaid: devinfo.oaid || '',
        ts: Date.now(),
        os_ver: devinfo.version_release || '',
        os_type: 'android',
        carrier: devinfo.operator || '',
        model: devinfo.model || '',
        regId: devinfo.reg_id || '',
        screen_width: devinfo.screen_width || '',
        screen_height: devinfo.screen_height || '',
        screen_density: devinfo.screen_density || '',
        user_id: userInfo.user_id || '',
        app_channel: devinfo.channel || '',
        app_dev: 'android_FE',
        imei,
        nonce: uuid(),
        eid: devinfo.eid || '', // 实验id
    };

    return queryObj;
}


function getUrl(url, query = {}) {
    const strQuery = queryString.stringify(query);

    if (strQuery) {
        return `${config.api.perfix}${url}?${strQuery}`;
    }

    return `${config.api.perfix}${url}`;
}

function handleParams(params) {
    Object.entries(params).forEach(([k, v]) => {
        if (v === undefined || v === null) {
            // eslint-disable-next-line no-param-reassign
            delete params[k];
        } else if (Array.isArray(v)) {
            // eslint-disable-next-line no-param-reassign
            params[k] = JSON.stringify(v);
        }
    });
    return params;
}
/** 前端获取签名方法 */

function getSign(data) {
    // eslint-disable-next-line no-param-reassign
    data = handleParams(data);
    const keys = Object.keys(data).sort();
    return md5(keys
        .reduce((pre, curr) => `${pre}&${curr}=${data[curr]}`, '')
        .slice(1)
        .concat('shi!@#$%^&*[xian!@#]*'),
    ).slice(4, 12);
}

function sign(data = {}) {
    const query = getCommonData();
    const signData = { ...data, ...query };
    const keys = Object.keys(signData);
    const nextSignData = {};

    keys.forEach((key) => {
        nextSignData[key] = String(signData[key]);
    });

    const strSignData = JSON.stringify(nextSignData);
    let requestSign = jsApi.getRequestKey(strSignData);
    if (!requestSign) {
        requestSign = getSign(signData);
    }

    query.requestSign = requestSign;

    return query;
}

function get(url, data = {}) {
    const query = sign(data);

    return axios.get(getUrl(url, query), { params: data });
}

function post(url, data = {}, options = {}) {
    const query = sign(data);

    return axios.post(
        getUrl(url),
        queryString.stringify({ ...query, ...data }),
        { headers: { 'Content-Type': 'application/x-www-form-urlencoded' }, ...options },
    );
}

function spost(url, data = {}, options = {}) {
    return axios.post(
        url,
        queryString.stringify(data),
        { headers: { 'Content-Type': 'application/x-www-form-urlencoded' }, ...options },
    );
}

