const router = new VueRouter({
    // eslint-disable-next-line space-before-function-paren
    scrollBehavior (to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }
        return { x: 0, y: 0 };
    },
    mode: 'history',
    base: '/mobile-v2/',
    routes,
});

router.beforeEach((_, from, next) => {
    router.from = from;

    next();
});

export default router;
