/**
 * 设置工具栏工具颜色（4.4.0）
 * true 设置为黑色
 * false 设置为白色
 */
export function changeStatusBarDarkMode(bool) {
    // 当页面可见时在改变工具栏颜色
    if (document.visibilityState === 'visible') {
        return tryCall('changeStatusBarDarkMode', bool);
    }

    return false;
}

