export function getImeiMd5() {
    const hex = '0123456789abcdef';

    if (!localStorage.imeiMd5) {
        localStorage.imeiMd5 = string.random(hex, 32);
    }

    return tryCall('getImeiMD5') || localStorage.imeiMd5;
}

